// Lists.
ul,
ol {
	font-size   : 1em;
	//line-height : 1.45;
	//margin      : 0 0 1.3em 0;
	padding-left: 2em;
	line-height: 2;
	margin: 0 0 2rem;

	@media ( max-width: break( 'mobile' ) ) {
		padding-left: 1em;
	}
}

// Remove bottom margin from lists that are direct children of a list item.
li > ul,
li > ol { margin-bottom : 0; }



